.friendList {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.item {
  display: flex;
  justify-content: space-evenly;
  min-width: 200px;
  list-style: none;
  margin: 10px 0px;
  background-color: lightblue;
}

.status {
  display: block;
  align-self: center;
  width: 10px;
  height: 10px;
  border-radius: 50%;
}

.ofline {
  background-color: red;
}

.online {
  background-color: green;
}
